import header from '@/components/header'
import API from '@/api/attendance'
import { MessageBox, Indicator, Toast } from 'mint-ui'
export default {
  data () {
    return {
      type: 'tab',
      address_detail: null,
      headtitle: '考勤打卡',
      showDkbtn: true, // 上班打卡显示
      showDkedbtn: true, // 下班打卡显示
      nowTime: '', // 当前时分
      nowDate: '', // 当前年月日
      userid: '',
      token: '',
      name: '', // 名字
      lon: '', // 定位经度
      lat: '', // 定位纬度
      morningdkTime: '', // 上午打卡时间
      noondkTime: '', // 下午打卡时间
      timeStage: '', // 上下班打卡标志
      address: '', // 打卡定位地址
      worktime: '' // 上班时间
    }
  },
  created () {
    this.userid = this.$store.getters.getUser.data.userid
    this.token = this.$store.getters.getUser.token
    this.name = this.$store.getters.getUser.data.name
    // 实时显示时间
    setInterval(this.getTime, 1000)
    let { userid, token } = this
    Indicator.open({
      text: '正在加载...',
      spinnerType: 'fading-circle'
    })
    // 判断是否打卡和早上打卡时间
    API.QUERYUSERDKTIME(this, { userid, token }).then((rep) => {
      let result = rep.body
      Indicator.close()
      if (result.success) {
        if (result.costomno === '5101080') {
          this.worktime = '09:00'
        } else if (result.costomno === '5101081') {
          this.worktime = '08:55'
        }
        let data = result.result
        if (data) {
          if (data.first_time) {
            this.showDkbtn = false
            this.morningdkTime = data.first_time
          } else {
            this.showDkbtn = true
          }
          if (data.last_time) {
            this.showDkedbtn = false
            this.noondkTime = data.last_time
          } else {
            this.showDkedbtn = true
          }
        }
      } else {
        MessageBox.alert(result.msg, '操作提示')
      }
    }, (rep) => {
      Indicator.close()
      Toast({
        message: '读取打卡信息失败',
        position: 'middle',
        duration: 1000
      })
    })
  },
  computed: {
    btnsbBg () {
      if (this.timeStage === 'sbclockin') {
        return 'sbdking'
      } else {
        return 'sbdked'
      }
    },
    btnxbBg () {
      if (this.timeStage === 'xbclockin') {
        return 'xbdking'
      } else {
        return 'xbdked'
      }
    }
  },
  methods: {
    getTime () {
      let today = new Date()
      var date = today.getFullYear() + '-' + this.twoDigits(today.getMonth() + 1) + '-' + this.twoDigits(today.getDate()) + ' '
      let time = this.twoDigits(today.getHours()) + ':' + this.twoDigits(today.getMinutes())
      this.nowTime = time
      this.nowDate = date
      // 判断是否超过12点
      if (today.getHours() * 1 <= 12) {
        this.timeStage = 'sbclockin' // 只能上班打卡
      } else {
        this.timeStage = 'xbclockin' // 只能下班打卡
      }
    },
    twoDigits (val) {
      if (val < 10) return '0' + val; return val
    },
    // 引用地图模块
    readyMap (callback) {
      const map = window.api.require('bMap')
      map.hide()
      // 实时定位
      map.getLocation({
        accuracy: '10m',
        autoStop: true,
        filter: 1
      }, function (ret, err) {
        if (ret.status) {
          this.lon = ret.lon
          this.lat = ret.lat
          // 获取两点之间的距离
          map.getDistance({
            start: {
              lon: this.lon,
              lat: this.lat
            },
            end: {
              lon: 104.0989520000,
              lat: 30.6755300000
            }
          }, function (ret) {
            if (ret.status) {
              // 获取定位的当前地址数据
              map.getNameFromCoords({
                lon: this.lon,
                lat: this.lat
              }, function (ret, err) {
                if (ret.status) {
                  this.address = ret.address
                }
              })
              callback(ret.distance, this.address, this.lon, this.lat)
            }
          })
        } else {
          alert(err.code)
        }
      })
    },
    dkEvent (type) {
      if (type === 'sbclockin' && this.timeStage === 'xbclockin') {
        MessageBox.alert('12:00过后无需打上班卡')
      } else if (type === 'xbclockin' && this.timeStage === 'sbclockin') {
        MessageBox.alert('12:00之前无需打下班卡')
      }
      if (this.timeStage === type) {
        this.readyMap((distance, address, longitude, latitude) => {
          if (distance < 100 && address) {
            let param = {
              userid: this.userid,
              token: this.token,
              address: address,
              longitude: longitude,
              latitude: latitude
            }
            Indicator.open({
              text: '正在加载...',
              spinnerType: 'fading-circle'
            })
            API.INSERTKQRECORD(this, param).then((rep) => {
              let result = rep.body
              Indicator.close()
              if (result.success) {
                if (type === 'sbclockin') {
                  this.morningdkTime = result.datetime
                  this.showDkbtn = false
                } else if (type === 'xbclockin') {
                  this.noondkTime = result.datetime
                  this.showDkedbtn = false
                }
              }
            }, (rep) => {
              Indicator.close()
              Toast({
                message: '打卡失败',
                position: 'middle',
                duration: 1000
              })
            })
          } else {
            MessageBox.alert('请在打卡范围内打卡', '操作提示')
          }
        })
      }
    }
  },
  components: {
    'v-header': header
  }
}



